@import '../../../styles/variables.scss';
@import '../../../styles/common.scss';

.contentDescription {
  position: absolute;
  top: 25%;
  left: 13.3vw;

  &__title {
    text-transform: uppercase;
    font-size: 4.6rem;
    font-weight: bold;
    line-height: 1.23;
    letter-spacing: 1.74px;
    color: $secondaryWhite;
  }
  &__info {
    width: 31vw;
    font-size: 1.8rem;
    line-height: 1.44;
    color: $secondaryWhite;
  }
  &__button {
    cursor: pointer;
    @include actionButtons(
      unset,
      $secondaryWhite,
      $primaryLightGreenBlue,
      5px,
      unset,
      unset
    );
    padding: 2.2rem 5.6rem;
    font-size: 1.6rem;
    text-transform: uppercase;
  }
}

@media screen and (max-width: 700px) {
  .contentDescription {
    position: absolute;
    top: 25%;
    left: 0;
    right: 0;
    text-align: center;
    display: grid;
    justify-items: center;
    &__title {
      text-transform: uppercase;
      font-size: 3.1rem;
      font-weight: bold;
      line-height: 1.23;
      letter-spacing: 1.74px;
      color: #ffffff;
      width: 22rem;
    }

    &__info {
      width: 27rem;
      font-size: 1.4rem;
      line-height: 1.44;
    }
  }
}
