@import '../../../styles/variables';
@import '../../../styles/common';

.cardItems__wrapper {
  @include displayCenterGrid(auto auto auto);
  grid-gap: 2.4rem;
  padding: 5.5rem 11rem 0;

  &__card {
    position: relative;
    object-fit: contain;
    border-radius: 0.5rem;
    cursor: pointer;
    transition: box-shadow 0.3s, transform 0.3s, opacity 0.3s;
    &:hover {
      box-shadow: 0 20px 30px 0 rgba(255, 255, 255, 1%);
      transform: translateY(-8px);
    }
    &--imageTitle {
      position: absolute;
      top: 3.15vw;
      left: 1.9vw;
      font-size: 1.25vw;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 0.4rem;
      letter-spacing: 0.096rem;
      color: $secondaryWhite;
      text-transform: uppercase;
      z-index: 1;
    }
    &--imageFooter {
      position: absolute;
      bottom: 1.57vw;
      left: 1.9vw;
      font-family: Rubik;
      font-size: 1.33vw;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.19;
      letter-spacing: 0.1rem;
      color: $secondaryWhite;
      text-transform: uppercase;
    }
    &--image {
      width: 100%;

      img {
        object-fit: contain;
        width: 100%;
      }
    }
  }
}
@keyframes example {
  from {
    top: 0;
  }
  to {
    top: -15px;
  }
}
@media screen and (max-width: 1024px) {
  .cardItems__wrapper {
    grid-gap: 1.8rem;
    padding: 4.5rem 6rem;
    &__card--imageTitle {
      left: 2.5rem;
    }
    &__card--imageFooter {
      font-size: 1.5vw;
    }
    &__card--image {
      width: 100%;
    }
  }
}

@media screen and (max-width: 767px) {
  .cardItems__wrapper {
    @include displayCenterGrid(auto);
    padding: 4.5rem 3rem 0;
    width: 100%;
    &__card--imageTitle {
      font-size: 2.1vw;
      top: 7.89vw;
      left: 6.57vw;
    }
    &__card--imageFooter {
      left: 6.57vw;
      bottom: 3vw;
      font-size: 2.5vw;
    }
    &__card--image {
      width: 100%;
    }
  }
}
